@import "src/styles/global.scss"; @import "portal/global.scss";















































.demo-nav {
    padding: 12px;
    box-sizing: border-box;
    .demo-nav-content {
        height: 100%;
        width: 100%;
        display: flex;
        & > div {
            height: 100%;
        }
        .demo-nav-left {
            width: 180px;
            padding-right: 12px;
            box-sizing: border-box;
            & > * {
                margin-bottom: 12px;
            }
        }
        .demo-nav-right {
            flex: 1;
            overflow: hidden;
        }
    }
}
